<template>
  <div class="container">

    <div class="row justify-content-center">
      <div class="card col-lg-5 p-5 shadow mt-5 signin__board">

        <div class="row logo flex justify-content-center mt-2">
          <div class="col-lg-8">
            <img src="images/prism_logo_color_mint.svg">
          </div>

          <div class="col-lg-12 text-center mb-3 font-weight-normal mt-2">
            Lifestyle online shop
          </div>
        </div>

        <form class="w-100" @submit.prevent.stop="handleSubmit">

          <div class="form-label-group mb-2">
            <label for="name">Name</label>
            <input
              id="name"
              v-model="name"
              name="name"
              type="text"
              class="form-control"
              placeholder="name"
              autocomplete="username"
              required
              autofocus
            />
          </div>

          <div class="form-label-group mb-2">
            <label for="email">Email</label>
            <input
              id="email"
              v-model="email"
              name="email"
              type="email"
              class="form-control"
              placeholder="email"
              autocomplete="email"
              required
            />
          </div>

          <div class="form-label-group mb-3">
            <label for="password">Password</label>
            <input
              id="password"
              v-model="password"
              name="password"
              type="password"
              class="form-control"
              placeholder="Password"
              autocomplete="new-password"
              required
            />
          </div>

          <div class="form-label-group mb-3">
            <label for="password-check">Password Check</label>
            <input
              id="password-check"
              v-model="passwordCheck"
              name="passwordCheck"
              type="password"
              class="form-control"
              placeholder="Password"
              autocomplete="new-password"
              required
            />
          </div>

        <gb-button
          type="submit"
          full-width
          class="mt-3 o-guide__button gb-base-button gb-base-button--turquoise gb-base-button--dark"
          :disabled="isProcessing"
        >
          {{ isProcessing ? '處理中...' : '送出' }}
        </gb-button>

          <div class="text-center mt-3 mb-3">
              <router-link to="/signin" >
                Sign In
              </router-link>
          </div>

          <p class="mt-5 mb-3 text-muted text-center">
            © 2020-2021
          </p>
        </form>

      </div>
    </div>

  </div>
</template>

<script>
import authorizationAPI from '../apis/authorization'
import { Toast } from '../utils/helpers'
export default {
  data() {
    return {
      name: '',
      email: '',
      password: '',
      passwordCheck: '',
      isProcessing: false
    }
  },
  methods: {
    async handleSubmit() {
      try{
        if(!this.name || !this.email || !this.password || !this.passwordCheck ) {
          this.toastFire('warning','請確認欄位是否填寫')
          return
        }
        if(this.password !== this.passwordCheck ) {
          this.toastFire('warning','請確認輸入的密碼是否相符')
          return
        }
        this.isProcessing = true
        const res = await authorizationAPI.signup({
          name: this.name,
          email: this.email,
          password: this.password,
          passwordCheck: this.passwordCheck
        })
        if (res.data.status !== 'success') {
          throw new Error(res.data.message)
        }
        this.$router.push('/signin')
        this.toastFire('success','您已成功註冊一組新帳號，現在您可以嘗試登入')
      } catch (err) {
        console.log('Signin error' ,err)
        this.isProcessing = false
        this.email = ''
        this.password = ''
        this.toastFire('warning', '無法註冊，請稍再試')
      }
    },
    toastFire(action,text){
      Toast.fire({
        icon: action,
        title: text,
        background: '#eee',
        showConfirmButton: false
      })
    }
  },
}
</script>
